<template>
	<view class="ticketBalance" style="padding-top:10rpx">
		
		<view class="tb-body" style="margin-top: 0rpx;">
			
			<view class="tb-content">
				<view class="tb-item" v-for="item in list" @click="goUrl(item.id)">
					<view class="tb-item-left">
						<image :src="item.picture_and_vedio[0].url" mode=""></image>
					</view>
					<view class="tb-item-right">
						<view class="t1">
							{{item.title}}
						</view>
						<view class="t2">
							{{item.start_time}}
						</view>
						<view class="t3">
							{{item.address}}
						</view>
						<view class="item-b" >
							<view class="price">￥<text>{{item.price}}</text></view>
							<view class="exchange">我要兑换</view>
						</view>
					</view>
				</view>

			</view>
		</view>

	</view>
</template>

<script>
	import {
		API
	} from '@/util/api.js';
	export default {
		data() {
			return {
				list: [],
				number: '',
				navIndex: 0,
				adapte_active:'',
				id:''
			}
		},
		onLoad(option) {
			console.log(option)
			// this.number = option.id
			this.adapte_active=option.adapte_active
			console.log(this.adapte_active)
			this.id=option.id
			this.getData();
		},
		methods: {
			trans() {

			},
			getData() {
				//获取演唱会列表
				this.request(API.getList, 'GET', {
					limit: 10,
					page: 1
				}).then(res => {
					console.log(res)
					if (res.code === 10000) {
						// this.list = res.data;
						//var areaIndex=this.area.findIndex(item => item.id == this.districtId)
						if(this.adapte_active!=''&&this.adapte_active!=undefined&&this.adapte_active!=0){
							var  aidIndex=res.data.findIndex(item => item.id == this.id)
							console.log(aidIndex)
						  this.list[0]=res.data[aidIndex]
						 this.$set(this.list,0, this.list[0])
						}else if(this.adapte_active==0){
							this.list = res.data;
						}
					}
				});
			},
			goUrl(id) {
				uni.navigateTo({
					url: '/pages/ticket/exchange?' + 'id=' + id+'&&adapte_active='+this.adapte_active
				});
				console.log(id)
			}
		}

	}
</script>

<style lang="scss">
	@import '../../assets/css/ticket/ticket.scss';
</style>
